import React from 'react';
import { default as MuiChip, ChipProps } from '@mui/material/Chip';

const Chip = React.forwardRef<HTMLElement, ChipProps>( ( props, ref ) => {
	return (
		<MuiChip
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<ChipProps>;

export default Chip;
